// Null__start
@import './scss/null';
// Null__end
// Breakpoints__start
@import './scss/breakpoints';
// Breakpoints__end
// Mixins__start
@import './scss/mixin';
// Mixins__end
body {
  font-family: 'Montserrat', sans-serif;
  min-width: 465px;
  background-color: #ffdf8c;
  padding: 40px;
}
.wrapper {
  max-width: 1340px;
  margin: 0 auto;
  background-color: #fff;
  box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.03);
  border-radius: 10px;
}
